.footerYear {
  align-self: end;
}

.footerBottom {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: relative;
  width: 100%;
  min-height: 35px;
  padding-bottom: 5px;
  background: rgba(0, 105, 92, 0.9);
  color: #fff;
}

.footerRsSchool {
  position: absolute;
  right: 23%;
  bottom: 50%;
  transform: translateY(50%);
}

.footerYear {
  align-self: flex-end;
}

.footerMask {
  position: absolute;
  width: 100%;
  height: 300px;
  background: rgba(0, 105, 92, 0.6);
}

.authorsWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 70%;
  height: 300px;
  position: relative;
  z-index: 100;
  margin: 0 auto;
}

.authorsTitle {
  margin: 0;
  margin-top: 80px;
  font-size: 1.6rem;
  color: #fff;
}

.authors {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 530px;
  font-size: 1.3rem;
  margin: 0;
  margin-top: 20px;
}

.author {
  color: #fff;
}

@media (max-width: 1300px) {
  .footerRsSchool {
    right: 10%;
  }
}

@media (max-width: 768px) {
  .footerRsSchool {
    right: 5%;
  }

  .authors {
    width: 80%;
  }

  .authorWrapper {
    width: 50%;
    text-align: center;
  }
}

@media (max-width: 454px) {
  .footerRsSchool {
    right: 1%;
  }

  .authors {
    width: 80%;
  }

  .authorWrapper {
    width: 100%;
    text-align: center;
  }
}
